$nav-padding: 0 30px;
$nav-padding-g1500: 0 60px;

.first {
    background-color: #fff;

    img {
        width: 64px;
        margin: 7px 0;
    }

    &.nav-padding {
        padding: $nav-padding;
    }

    @media (min-width: 1500px) {
        &.nav-padding {
            padding: $nav-padding-g1500;
        }
    }
}

.second {
    background-color: #51007a;

    img {
        width: 150px;
        padding: 17px 0;
    }

    &.nav-padding {
        padding: $nav-padding;
    }

    @media (min-width: 1500px) {
        &.nav-padding {
            padding: $nav-padding-g1500;
        }
    }
}
